.advanage-row {
	margin-top: -24px;
	margin-bottom: -24px;
}

.advanage {
	display: flex;
	flex-direction: column;
	text-align: center;
	margin: 24px auto;
	max-width: 263px;
	padding: 0 12px;

	&-img-box {
		width: 93px;
		height: 93px;
		display: flex;
		align-items: center;
		justify-content: center;
		background: $color-secondary;
		border-radius: 50%;
		margin: 10px auto 32px;
		position: relative;
		padding: 15px;

		&:before {
			content: "";
			border: 1px solid #6c7188;
			display: inline-block;
			width: 113px;
			height: 113px;
			position: absolute;
			left: -10px;
			top: -10px;
			border-radius: 50%;
		}
	}

	.advantage-img {
		max-width: 100%;
	}
}
#advanages-slider {
	.slick-dots {
		padding: 0;
		margin: 15px 0;
	}
}
